.scale-control{
 position: relative;
 margin-right: 10px;
 display: flex;
}

.scale-button{
 width: 30px;
 height: 30px;
 border-radius: 5px;
 margin-right: 1px;
 line-height: 30px;
 cursor: pointer;
}

.dropdown{
 width: 40px;
 height: 30px;
 border-radius: 5px;
 font-size: 15px;
 margin-right: 11px;
}

.dropdown-button{
 width: 50px;
 height: 30px;
 border-radius: 5px; 
 margin-right: 5px; 
}
.dropdown-field{
 position: absolute;
 top:30px;
 left: 5px;
 margin-right: 20px;
 width: 100px;
 height: 200px;
 background-color: white;
 color: black;
 cursor: pointer;
 overflow: scroll;
}
